<template>
	<div class="pointsDetails">
		<div class="pointsDetails_head">
			<i class="icon_arrow_left_black" @click="goBack"></i>
			<p>商品详情</p>
		</div>
		<div class="swiper">
			<van-swipe @change="onChange">
			  <van-swipe-item v-for="item in detailsObj.imgList" :key="item.id">
				  <img :src="item.imgUrl" class="img_swiper">
			  </van-swipe-item>
			  <template #indicator>
			    <div class="custom-indicator">{{ current + 1 }}/{{ detailsObj.imgList.length }}</div>
			  </template>
			</van-swipe>
		</div>
		<p class="title1">{{ itemData.commodityName }}</p>
		<div class="title">
			<p class="title2">
				<span>{{ itemData.amount }}福利值</span>
				<span>剩余：{{ itemData.stockRemain }}</span>
			</p>
			<van-divider  :style="{ margin: '0', padding: '0' }" :hairline="false"/>
			<p class="title3">温馨提示:抵扣券仅限充值时抵扣，不支持直接充值</p>
		</div>
		<div class="divider_box">
			<van-divider :hairline="false"><span class="divider">商品详情</span></van-divider>
		</div>
		<ul class="pointsDetails_list">
			<li v-for="(item,index) in itemData.commodityInfoImg" :key="index">
				<img :src="item" alt="">
			</li>
		</ul>
		<p :class="itemData.stockRemain == 0?'btn_exchange act':'btn_exchange'" @click="toPlaceOrder">{{ itemData.stockRemain == 0?'已兑光':'立即兑换' }}</p>
	</div>
</template>

<script>
	export default{
		name:'pointsDetails',
		data(){
			return{
				current: 0,
				itemData:{},
				detailsObj:{
					imgList:[
						{
							id:0,
							imgUrl:'https://img01.yzcdn.cn/vant/apple-1.jpg'
						},
						{
							id:1,
							imgUrl:'https://img01.yzcdn.cn/vant/apple-2.jpg'
						},
						{
							id:2,
							imgUrl:'https://img01.yzcdn.cn/vant/apple-3.jpg'
						},
						{
							id:3,
							imgUrl:'https://img01.yzcdn.cn/vant/apple-4.jpg'
						}
					],
					
				}
			}
		},
		mounted() {
			this.itemData = this.$route.query.item;
		},
		methods:{
			goBack(){
				this.$router.go(-1);
			},
			onChange(index) {
				this.current = index;
			},
			toPlaceOrder(){
				let _this = this;
				if(_this.itemData.stockRemain == 0){
					_this.$toast('库存不足～');
				}else{
					_this.$router.push({path:'/placeOrder',query:{itemData:_this.itemData}});
				}
			},
			
		}
	}
</script>

<style scoped>
	.pointsDetails{
		width: 10.8rem;
		min-height: 100vh;
		background-color: #f4f4f4;
		padding-bottom:2rem;
	}
	.pointsDetails_head{
		width: 100%;
		height: 1.51rem;
		position: relative;
		background-color: #fff;
		padding-top:var(--barHeight);
		border-bottom:1px solid #e1e1e1;
	}
	.icon_arrow_left_black{
		display: inline-block;
		vertical-align: middle;
		width:0.56rem;
		height:0.56rem;
		background-image: url(../static/img/icon_arrow_left_black.png);
		background-repeat: no-repeat;
		background-size: 100%;
		position: relative;
		z-index: 4;
		margin:0.47rem 0 0 0.33rem;
	}
	.pointsDetails_head p{
		width: 100%;
		height: 1.51rem;
		line-height: 1.51rem;
		position: absolute;
		bottom:0;
		left:0;
		z-index: 2;
		text-align: center;
		color:#000;
		font-size: 0.48rem;
	}
	.img_swiper{
		width: 10.8rem;
		height: 10.8rem;
	}
	.custom-indicator{
		display: inline-block;
		color:#fff;
		background-color: rgba(0,0,0,0.3);
		border-radius: 0.36rem;
		line-height: 0.72rem;
		padding:0 0.2rem;
		font-size: 0.36rem;
		position: absolute;
		bottom:0.27rem;
		right: 0.25rem;
	}
	.swiper{
		position: relative;
	}
	.divider{
		font-size: 0.4rem;
		color: #727272;
	}
	.title{
		background-color: #fff;
		box-sizing: border-box;
		padding:0 0.23rem;
	}
	.title1{
		line-height: 1.46rem;
		height: 1.46rem;
		width: 100%;
		background-image: linear-gradient(to right,#ff3c3c,#ff1f59);
		color:#fff;
		font-size: 0.72rem;
		box-sizing: border-box;
		padding:0 0.48rem;
	}
	.title2{
		display: flex;
		justify-content: space-between;
		line-height: 1.32rem;
		font-size: 0.48rem;
		box-sizing: border-box;
		padding:0 0.25rem;
	}
	.title2 span{
		display: inline-block;
		vertical-align: middle;
	}
	.title3{
		font-size: 0.36rem;
		color:#848383;
		line-height: 1.12rem;
		box-sizing: border-box;
		padding:0 0.25rem;
	}
	.divider_box{
		box-sizing: border-box;
		padding:0 2.9rem;
	}
	.btn_exchange{
		width: 8rem;
		height:1.2rem;
		border-radius: 0.6rem;
		background-color: #ff588c;
		color:#fff;
		text-align: center;
		line-height: 1.2rem;
		position: fixed;
		bottom:0.25rem;
		left:50%;
		margin-left:-4rem;
	}
	.btn_exchange.act{
		background-color: #cacaca;
		color:#000;
	}
	.pointsDetails_list{
		box-sizing: border-box;
		padding:0.33rem;
	}
	.pointsDetails_list img{
		width: 100%;
	}
</style>
